import React, {Component} from 'react';
import {Field, reduxForm,formValueSelector,autofill} from 'redux-form';
import {Form, Button, Row, Col, Modal} from 'react-bootstrap';
import InputField from '../custom-bootstrap/InputField';
import {signUp} from '../../firebase/auth';
import {connect} from 'react-redux';
import ReactDOM from 'react-dom';
import {createTextMask} from 'redux-form-input-masks';
import {validateCNPJ} from '../../utils/cnpjUtils';
import {states} from '../../data/locale';
import axios from 'axios';

class SignupCompanyForm extends Component {
    constructor(props) {
        super(props);
        this.state = {showTerms: false};
    }

    handleShowTerms = () => this.setState({showTerms: true});

    handleCloseTerms = () => this.setState({showTerms: false});

    submit = (values) => {
        delete values.termsAndConditions;
        signUp(values, true);
    }

    componentDidUpdate(prevProps){
        if(this.props.province !== prevProps.province) {
            console.log(this.props);
            const provinceField = ReactDOM.findDOMNode(this.provinceField).children[1];
            axios.get(`https://servicodados.ibge.gov.br/api/v1/localidades/estados/${provinceField.options[provinceField.selectedIndex].getAttribute('data-id')}/municipios`)
            .then((response) => this.setState({cities: response.data}));
        }
    }

    render() {
        const {submitting, handleSubmit} = this.props;
        return (
            <div>
                <h1>Crie a conta da sua empresa</h1>
                <Form onSubmit={handleSubmit(this.submit)}>
                    <Row>
                        <Col>
                            <Field
                                name="cnpj"
                                type='text'
                                component={InputField}
                                label="CNPJ"
                                placeholder="Ex.: 00.000.000/0000-00"
                                {...cnpjMask}/>
                        </Col>
                        <Col>
                            <Field
                                name="name"
                                type='text'
                                component={InputField}
                                label="Razo Social"
                                placeholder="Ex.: Apple Inc."/>
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <Field
                                name="email"
                                type='email'
                                component={InputField}
                                label="E-mail"
                                placeholder="Ex.: johndoe@email.com"/>
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <Field
                                name="zipCode"
                                component={InputField}
                                type="text"
                                label="CEP"
                                placeholder="Ex.: 00.000-000"
                                {...zipCodeMask}/>
                        </Col>
                    </Row>
                    <Row>
                        <Col md={6}>
                            <Field
                                name="street"
                                component={InputField}
                                type="text"
                                label="Endereo"
                                placeholder="Ex.: Rua do Z"/>
                        </Col>
                        <Col md={3}>
                            <Field
                                name="number"
                                component={InputField}
                                type="text"
                                label="Nmero"
                                placeholder="Ex.: 33"/>
                        </Col>
                        <Col md={3}>
                            <Field
                                name="complement"
                                component={InputField}
                                type="text"
                                label="Bloco/Andar"
                                placeholder="Ex.: 9 andar, Bloco 5"/>
                        </Col>
                    </Row>
                    <Row>
                        <Col md={6}>
                            <Field
                                name="neighborhood"
                                component={InputField}
                                type="text"
                                label="Bairro"/>
                        </Col>
                        <Col md={6}>
                            <Field
                                ref={(provinceField) => {this.provinceField = provinceField}}
                                name="province"
                                component={InputField}
                                type="select"
                                label="Estado"
                                placeholder="Selecione um estado..." onChange={this.getCities}>
                                {states.sort((p1,p2) => p1.nome > p2.nome ? 1 : -1).map((province) => <option key={province.id} data-id={province.id} value={province.sigla}>{province.nome}</option>)}
                            </Field>
                        </Col>
                    </Row>
                    {this.props.province && this.state.cities ? 
                        <Row>
                            <Col>
                                <Field
                                    name="city"
                                    component={InputField}
                                    type="select-search"
                                    label="Cidade"
                                    options={this.state.cities.map(city => ({value:city.nome,label:city.nome}))}
                                    placeholder="Ex.: Belo Horizonte">
                                </Field>
                            </Col>
                        </Row> : null}
                    <Row>
                        <Col>
                            <Field
                                name="password"
                                component={InputField}
                                type="password"
                                label="Senha"
                                placeholder="No mnimo 6 caracteres"/>
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <Field
                                style={{display: 'inline-block'}}
                                name="termsAndConditions"
                                component={InputField}
                                type="checkbox"
                                label={"Li e aceito os "}
                                labellink={
                                    <Button variant="link" style={{padding:0,verticalAlign:'baseline'}}
                                    onClick={this.handleShowTerms}>
                                    termos de condies</Button>
                                }
                                placeholder="No mnimo 6 caracteres"/>
                                <div style={{width:'5px',height:'auto',display:'inline-block'}}/>
                        </Col>
                    </Row>

                    <Button disabled={submitting} block type="submit" size="lg">Criar conta</Button>
                </Form>
                <Modal size="lg" show={this.state.showTerms} onHide={this.handleCloseTerms}>
                    <Modal.Header closeButton>Termos e condies</Modal.Header>
                    <Modal.Body>
                        TERMOS E CONDIES
                    </Modal.Body>
                </Modal>
            </div>
        );
    }
};

const cnpjMask = createTextMask({pattern: '99.999.999/9999-99',guide:false});
const zipCodeMask = createTextMask({pattern: '99.999-999', guide: false});

const validate = values => {
    const errors = {}
    if (!values.name) 
        errors.name = 'Campo obrigatrio.';
    if (!validateCNPJ(values.cnpj)) 
        errors.cnpj = 'CNPJ invlido.'
    if (!values.cnpj) 
        errors.cnpj = 'Campo obrigatrio.';
    if(!values.zipCode)
        errors.zipCode = 'Campo obrigatrio.';
    if(!values.street)
        errors.street = 'Campo obrigatrio.';
    if(!values.neighborhood)
        errors.neighborhood = 'Campo obrigatrio.';
    if(!values.number)
        errors.number = 'Campo obrigatrio.';
    if (!values.email) 
        errors.email = 'Campo obrigatrio.';
    if (!values.termsAndConditions) 
        errors.termsAndConditions = 'Voc deve aceitar os termos para criar sua conta.';
    else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) 
        errors.email = 'Email Invlido';
    if (!values.password) 
        errors.password = 'Campo obrigatrio.';
    else if (values.password.length < 6) 
        errors.password = 'A senha deve conter pelo menos 6 caracteres';
    return errors;
}

const asyncValidate = (values, dispatch) => {
    if(values.zipCode && values.zipCode.length === 8)
     return axios.get(`https://viacep.com.br/ws/${values.zipCode}/json/ `)
            .then((response) => {
                const {data} = response;
                dispatch(autofill('signupCompany','street',data.logradouro));
                dispatch(autofill('signupCompany','city',data.localidade));
                dispatch(autofill('signupCompany','neighborhood',data.bairro));
                dispatch(autofill('signupCompany','province',data.uf));
                if(data.erro) {
                   const error = {zipCode: 'CEP invlido.'}; throw error;
                }
            });
    else return new Promise((resolve) => { const error = {zipCode: 'CEP invlido.'}; throw error});
}

const selector = formValueSelector('signupCompany');

export default connect(state => {
    const zipCode = selector(state, 'zipCode');
    const province = selector(state, 'province');
    return {zipCode,province};
})(reduxForm({form: 'signupCompany', validate, asyncValidate, asyncBlurFields: ['zipCode']})(SignupCompanyForm));